        this.ballIcon = 'el-icon-right';
      } else {
        this.ballIcon = 'el-icon-more';
      }

      var x = this.$refs['ballBtn'].$el.getBoundingClientRect().x;
      var w = document.documentElement.clientWidth;
      //console.log('点击事件x:'+x+",w:"+w/2);
      this.ballShow = !this.ballShow;
      if (x > (w / 2)) {//大于窗口一半
        document.getElementById("buttonPlatfromUser").style.left = this.menuLeft + 'px';
      } else {//小于窗口一半
        document.getElementById("buttonPlatfromUser").style.left = Math.abs(this.menuLeft) + 'px';// '70px';
      }
