      var data_id = year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
      if (row.lai_start_time > data_id) {
        this.type = 'A'
      } else if (row.lai_start_time <= data_id) {
        this.type = 'B'
      }
      var lau_aci_uuid = row.lai_uuid;
      var lai_caption = row.lai_caption;
      var type = this.type;
      var lai_detail_status = row.lai_detail_status;
      Bus.$emit("globalMsgForTabPage", {
        method: "open",
        id: "M_B_20210924_095215930_100510839",
        caption: lai_caption,
        url: "/ClientManagement?lau_aci_uuid=" + lau_aci_uuid + "&type=" + type + "&lai_caption=" + lai_caption + "&lai_detail_status=" +lai_detail_status,
        vue: "/views/bankPlatform/deviceIndex/ClientManagement.vue"
      });
