      this.platformRoleForm.lai_content = row.lai_content;
      this.platformRoleForm.lai_caption = row.lai_caption;
      this.platformRoleForm.lai_user_scope = row.lai_user_scope;
      if (row.lai_waring_time === '' || row.lai_waring_time === undefined) {
        this.checked = false;
      } else if (row.lai_waring_time !== '' || row.lai_waring_time !== undefined) {
        this.checked = true;
      }
      if (row.lai_user_scope === '1') {
        this.phone = false;
        this.radio = '1';
      } else if (row.lai_user_scope === '2') {
        this.phone = true;
        this.radio = '2';
      }

      this.data_time1 = this.customGlobal.getDateTimeToTimestamp(this.platformRoleForm.lai_start_time);
      this.data_time2 = this.customGlobal.getTimestampToDateTime((this.customGlobal.getDateTimeToTimestamp(this.platformRoleForm.lai_start_time) - this.platformRoleForm.lai_waring_time * 84600 * 1000));
      this.platformRoleForm.lai_uuid = row.lai_uuid;
      if (this.checked === true) {
        this.platformRoleForm.lai_waring = '2';
      } else if (this.checked === false) {
        this.platformRoleForm.lai_waring = '1';
      }
      this.select_Activities_gift();
